'use client';

import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/Card';

const data = [
  { name: 'Jan', completed: 4, inProgress: 2 },
  { name: 'Feb', completed: 3, inProgress: 5 },
  { name: 'Mar', completed: 6, inProgress: 3 },
  { name: 'Apr', completed: 8, inProgress: 4 },
  { name: 'May', completed: 5, inProgress: 6 },
  { name: 'Jun', completed: 7, inProgress: 2 },
];

export default function Analytics() {
  return (
    <Card className="col-span-1 lg:col-span-2">
      <CardHeader>
        <CardTitle>Project Analytics</CardTitle>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={300}>
          <BarChart data={data}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey="completed" fill="#82ca9d" name="Completed" />
            <Bar dataKey="inProgress" fill="#8884d8" name="In Progress" />
          </BarChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );
}